import React from "react";
import { Select } from "antd"


export default function VariableTypeSelect(props) {
    return (
        <Select
            {...props}
            defaultValue={"String"}
            style={{ width: "100%" }}
        >
            <Select.Option value={"String"}>String</Select.Option>
            <Select.Option value={"Bool"}>Bool</Select.Option>
        </Select>
    );
}
